<template>
  <v-card flat>
    <v-snackbar v-model="snackbar" absolute top right color="success">
      게시물이 등록되었습니다.
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>

    <v-form ref="form" @submit.prevent="submit">
      <v-container fluid>
        <v-row>
          <v-col cols="12" sm="12">
            <v-text-field
              v-model="form.title"
              :rules="rules.title"
              color="purple darken-2"
              label="제목"
              required
            ></v-text-field>
          </v-col>
          <v-col cols="12">
            <small>내용</small>

            <div class="my-5">
              <div v-if="editor">
                <v-btn
                  @click="setLink"
                  :class="{ 'is-active': editor.isActive('link') }"
                >
                  <v-icon>mdi-link-variant</v-icon>
                </v-btn>
                <v-btn
                  @click="
                    editor
                      .chain()
                      .focus()
                      .unsetLink()
                      .run()
                  "
                  :disabled="!editor.isActive('link')"
                >
                  <v-icon>mdi-link-variant-off</v-icon>
                </v-btn>
                <v-btn
                  @click="
                    editor
                      .chain()
                      .focus()
                      .setHardBreak()
                      .run()
                  "
                >
                  <v-icon>mdi-wrap</v-icon>
                </v-btn>
                <editor-content :editor="editor" />
              </div>
            </div>
            <div>
              <EditorContent :editor="editor" />
            </div> </v-col
          ><v-col cols="12" sm="12">
            <v-text-field
              v-model="form.link"
              :rules="rules.link"
              color="purple darken-2"
              label="YouTube 링크"
            ></v-text-field>
          </v-col>
          <v-col cols="12" sm="12">
            <v-select
              v-model="form.category"
              :items="categories"
              :rules="rules.category"
              color="pink"
              label="카테고리"
              required
            ></v-select>
          </v-col>
        </v-row>
      </v-container>
      <v-card-actions>
        <v-btn
          class="my-5 ml-3"
          depressed
          color="#9575cd"
          dark
          onclick="history.back()"
          >뒤로가기</v-btn
        >
        <v-spacer></v-spacer>
        <v-btn text @click="resetForm">
          초기화
        </v-btn>
        <v-btn :disabled="!formIsValid" text color="primary" type="submit">
          등록하기
        </v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>
<script>
import axios from 'axios';
import { Editor, EditorContent } from '@tiptap/vue-2';
import StarterKit from '@tiptap/starter-kit';

import HardBreak from '@tiptap/extension-hard-break';
import Paragraph from '@tiptap/extension-paragraph';

import Link from '@tiptap/extension-link';

export default {
  name: 'PostCreateForm',
  components: {
    EditorContent,
  },
  data() {
    const defaultForm = Object.freeze({
      title: '제목',
      // content: '',
      link: 'https://youtu.be/j9KSwmqFdjU',
      category: '비디오 컨텐츠',
    });

    return {
      form: Object.assign({}, defaultForm),
      rules: {
        category: [(val) => (val || '').length > 0 || '필수입력'],
        title: [(val) => (val || '').length > 0 || '필수입력'],
        // content: [(val) => (val || '').length > 0 || '필수입력'],
        link: [(val) => (val || '').length > 0 || '링크 주소를 확인하세요'],
      },
      categories: ['비디오 컨텐츠', '크레파스 워십'],
      snackbar: false,
      defaultForm,
      editor: null,
    };
  },

  mounted() {
    this.editor = new Editor({
      content: '',
      extensions: [
        StarterKit,
        Link.configure({
          openOnClick: true,
          autolink: true,
          linkOnPaste: true,
          HTMLAttributes: {
            class: 'my-custom-class',
          },
        }),
        HardBreak.configure({
          keepMarks: true,
        }),
        Paragraph,
      ],
    });
  },

  computed: {
    formIsValid() {
      // return this.form.title && this.form.content && this.form.category;
      return this.form.title && this.form.category;
    },
  },

  methods: {
    resetForm() {
      this.form = Object.assign({}, this.defaultForm);
      this.$refs.form.reset();
    },
    async submit() {
      await axios
        .post(
          `${this.$ROOT_API}/posts`,
          {
            title: this.form.title,
            content: this.editor.getHTML(),
            youtubeUrl: this.form.link.split('youtu.be/')[1],
            category: this.form.category,
          },
          {
            headers: {
              Authorization: `Bearer ${window.localStorage.getItem('token')}`,
            },
          }
        )
        .then((res) => {
          return res.data;
        })
        .then((this.snackbar = true), this.resetForm())
        .catch((err) => {
          console.log(err);
        });
    },
    setLink() {
      const previousUrl = this.editor.getAttributes('link').href;
      const url = window.prompt('URL', previousUrl);

      // cancelled
      if (url === null) {
        return;
      }

      // empty
      if (url === '') {
        this.editor
          .chain()
          .focus()
          .extendMarkRange('link')
          .unsetLink()
          .run();

        return;
      }

      // update link
      this.editor
        .chain()
        .focus()
        .extendMarkRange('link')
        .setLink({ href: url })
        .run();
    },
  },
};
</script>
<style lang="scss">
.ProseMirror {
  min-height: 300px;
  border: 2px solid;
  border-radius: 5px;
}
.ProseMirror {
  a {
    color: #68cef8;
  }

  code {
    font-size: 0.9rem;
    padding: 0.25em;
    border-radius: 0.25em;
    background-color: rgba(#616161, 0.1);
    color: #616161;
    box-decoration-break: clone;
  }
}
</style>
